<template>
    <div class="page-detail" v-show="data.price">
        <div class="image-part">
            <img :src="data.img" alt="">
            <h1>{{data.title}}</h1>
            <p>{{data.description}}</p>
        </div>
        <div class="price-part">
            <span class="price"><strong>{{data.price}}</strong>元</span>
            <span class="origin-price">门市价:{{data.originPrice}}元</span>
            <span class="number">
                <span @touchend="add">+</span>
                <span>{{($store.state.products.find(product => product._id === data._id) || {count : 0}).count}}</span>
                <span @touchend="delet">-</span>
            </span>            
            <!-- <span :class="{
            'btn-buy': true,
            'has-buy': data.hasBuy
        }" @touchend="buyItem">{{data.hasBuy ? '取消购买' : '立即购买'}}</span> -->
            <span :class="{
            'btn-buy': true,
            'has-buy': true
        }"  @touchend="buyItem">取消购买</span>
        </div>
        <div class="sales-part">
            <li>支持立即退货</li>
            <li>支持随时退货</li>
            <li>销量:{{data.sales}}</li>
        </div>
        <div class="store-part module">
            <div class="module-header">店家信息</div>
            <div class="module-body">
                <p>{{data.storeName}}</p>
                <p>{{data.storeAddress}}</p>
            </div>
            <div class="module-footer">查看其它{{data.storeNum}}家分店</div>
        </div>
        <div class="notice-part module">
            <div class="module-header">购买须知</div>
            <div class="module-body" v-html="data.content"></div>
        </div>
        <ShoppingCar></ShoppingCar>
    </div>
</template>
<style lang="scss">
@import '@/common.scss';

.page-detail {
    padding-bottom: 70px;

    .image-part {
        position: relative;

        img {
            width: 100%;
            display: block;
        }

        h1,
        p {
            position: absolute;
            bottom: 35px;
            left: 10px;
            color: #fff;
        }

        h1 {
            font-size: 24px;
        }

        p {
            bottom: 10px;
            font-size: 15px;
        }
    }

    .price-part {
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        padding: 0 10px;
        height: 50px;
        line-height: 50px;
        font-size: 16px;

        .price {
            color: $color;
            margin-right: 8px;

            strong {
                font-size: 24px;
                font-weight: normal;
            }
        }

        .btn-buy {
            float: right;
            padding: 5px 12px;
            color: #fff;
            background-color: #f30;
            font-size: 12px;
            line-height: 1;
            border-radius: 4px;
            margin-top: 14px;
        }

        .has-buy {
            color: #333;
            background-color: #ddd;
        }

        .origin-price {
            font-size: 15px;
            color: #ccc;
            text-decoration: line-through;
        }

        .number{
            margin-left: 50px;
            span{
                text-align: center;
                display: inline-block;
                width: 20px;
                height: 20px;
                line-height: 20px;
                background:$color;
                margin-left: 2px;
                border-radius: 10px;
                color:#fff;
            }
            span:nth-child(2){
                background: #fff;
                color:#333;

            }
        }

    }

    .sales-part {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        padding: 10px 0;

        li {
            width: 50%;
            color: yellowgreen;
            padding-left: 10px;
            line-height: 26px;
            box-sizing: border-box;

            &:last-child {
                color: #000;
            }
        }
    }

    .module {
        margin-top: 10px;
        background-color: #fff;
        padding: 10px;

        .module-header {
            border-bottom: 1px solid #ccc;
            padding: 8px 0 10px;
            font-size: 20px;
            font-weight: bold;

        }

        .module-body {
            padding: 10px 0;
            font-size: 13px;
            line-height: 28px;

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                font-weight: normal;
                font-size: 15px;
                color: #f30;
            }
        }

        .module-footer {
            border-top: 1px solid #ccc;
            padding: 10px 0 8px;
            font-size: 10px;
            color: skyblue;
        }
    }
}
</style>
<script>
import ShoppingCar from '@/components/ShoppingCar';
export default {
    components: { ShoppingCar },
    data() {
        return {
            data: {},
            car:[]
        }
    },
    methods: {
        getData() {
            let { params } = this.$route;
            this.$http
                .get('/data/detail', { params })
                .then(({ data }) => {
                    if (data) {
                        console.log(data)
                        if (this.$store.state.products.find(item => item._id === data._id)) {
                            data.hasBuy = true
                        }
                        this.data = data;
                    } else {
                        this.$router.go(-1);
                    }
                })
        },
        buyItem() {
            // this.$store.commit(this.data.hasBuy ? 'deleteItem' : 'addItem', this.data)
            // this.$set(this.data, 'hasBuy', !this.data.hasBuy);

            this.$store.commit('reset');
            this.$router.back();
        },
        add() {
            // console.log(this.data)
            this.$store.commit('addItem',this.data);
        },
        delet() {
            this.$store.commit('deleteItem',this.data);
        }
    },
    created() {
        this.getData();
    },
    watch: {
        $route() {
            this.getData();
        }
    }



}
</script>